<template>
  <div class="card-detail">
    <div class="top-bt">
      <el-popconfirm
        title=""
        class="revoke"
        confirm-button-text=""
        cancel-button-text=""
        @confirm="deleteWall"
        icon="el-icon-info"
      >
        <template #reference>
          <p class="revoke"></p>
        </template>
      </el-popconfirm>

      <!-- <p class="revoke" @click="deleteWall"></p> -->
      <p
        class="report"
        @click="toAlert(',~')"
      >
        
      </p>
    </div>
    <slot class="noteCard"></slot>
    <!-- <NodeCard v-if="cardData" :cardData="cardData" class="noteCard" ></NodeCard> -->
    <!--  -->
    <div class="form">
      <textarea
        class="message"
        placeholder="..."
        v-model="message"
      ></textarea>
      <div class="bt">
        <input type="text" class="name" placeholder="" v-model="name" />
        <LyButton
          class="buttom"
          :disabled="name.trim() == '' || message.trim() == ''"
          @click="submit"
          ></LyButton
        >
      </div>
    </div>
    <!--  -->
    <p class="title"> {{ cardData.comcount[0].count }}</p>
    <div class="comments">
      <div class="comment-li" v-for="(item, index) in comments" :key="index">
        <div
          class="user-head"
          :style="{ background: portrait[item.imgurl] }"
        ></div>
        <div class="comment-right">
          <div class="momment-right-top">
            <p class="name">{{ item.name }}</p>
            <p class="time">{{ item.moment }}</p>
            <el-popconfirm
              title=""
              class="delete"
              confirm-button-text=""
              cancel-button-text=""
              @confirm="deleteComment(item.id)"
              icon="el-icon-info"
            >
              <template #reference>
                <p class="delete"></p>
              </template>
            </el-popconfirm>

            <!-- <p class="delete" @click="deleteComment(item.id)"></p> -->
          </div>
          <div class="comment-message">
            {{ item.comment }}
          </div>
        </div>
      </div>
    </div>
    <p class="more" @click="getComment" v-show="nowPage > 0">...</p>
  </div>
</template>

<script>
// import NodeCard from "@/components/NodeCard.vue";
import LyButton from "./LyButton.vue";

import { portrait } from "@/utils/data";
import { formatDate } from "@/utils/lysg.index";
import {
  insertCommentApi,
  findCommentPageApi,
  deleteWall,
  deleteComment,
} from "@/api/index";
export default {
  name: "CardDetail",
  props: {
    cardData: {
      default: {},
    },
    index: {
      default: 0,
    },
  },
  components: {
    // NodeCard,
    LyButton,
  },
  data() {
    return {
      comments: [],
      portrait,
      message: "", //
      name: "", //
      userId: this.$store.state.user,
      // cardDatas: {},
      nowPage: 1, //
      pageSize: 4, //
      // add: 0,
      isCurEnd: true, //
    };
  },

  computed: {
    cardDatas() {
      return this.cardData;
    },
  },
  watch: {
    cardData() {
      this.add = 0;
      this.nowPage = 1;
      this.comments = [];
      this.getComment();
    },
    name(newvalue) {
      if (newvalue.length > 5) {
        this.name = this.name.slice(0, 5);
        this.$message({
          type: "warning",
          message: "~",
        });
      }
    },
    message(newvalue) {
      if (newvalue.length > 50) {
        this.message = this.message.slice(0, 50);
        this.$message({
          type: "warning",
          message: "~",
        });
      }
    },
  },
  methods: {
    //
    submit() {
      //
      let img = Math.floor(Math.random() * 9);
      let name = "";
      if (this.name) {
        name = this.name;
      }
      let data = {
        wallId: this.cardData.id,
        userId: this.userId.id,
        imgurl: String(img),
        moment: formatDate(new Date()),
        comment: this.message,
        name: name,
      };
      insertCommentApi(data).then((res) => {
        if (res.code == 200) {
          this.comments.unshift(data);
          this.nowPage = 1;
          this.comments = [];
          this.getComment();
          // this.add++;
          this.message = "";
          this.name = "";
          this.$emit("dealAddOrDeleteComment", 1);
        }
      });
    },
    //
    getComment() {
      if (this.nowPage > 0 && this.isCurEnd) {
        let data = {
          id: this.cardData.id,
          page: this.nowPage,
          pagesize: this.pageSize,
        };

        this.isCurEnd = false; //
        findCommentPageApi(data).then((res) => {
          this.comments = this.comments.concat(res.message);
          if (res.message.length > 0) {
            this.nowPage++;
            this.isCurEnd = true; //
          } else {
            this.nowPage = 0;
            this.isCurEnd = true; //
          }
        });
      }
    },

    //
    toAlert(message) {
      alert(message);
    },
    //
    deleteWall() {
      let data = {
        id: this.cardData.id,
        imgurl: this.cardData.imgurl,
      };
      deleteWall(data).then((res) => {
        if (res.code == 200) {
          this.$message({
            message: "",
            type: "success",
          });
          this.$emit("closeModel");
          this.$emit("dealDeleteWall");
        }
      });
    },
    //
    deleteComment(id) {
      let data = {
        id: id,
      };
      deleteComment(data).then((res) => {
        if (res.code === 200) {
          this.$message({
            message: "",
            type: "success",
          });

          this.comments = [];
          this.nowPage = 1;

          this.getComment();

          this.$emit("dealAddOrDeleteComment", -1);
        }
      });
    },
  },
  mounted() {
    this.getComment(); //
  },
};
</script>

<style lang="less" scoped>
.card-detail {
  padding: 0 30px;
  height: 620px;
  overflow: auto;
  /*  */
  &::-webkit-scrollbar {
    /*  */
    width: 3px;
    /*  */
    // height: 5px;
  }
  /*  */
  &::-webkit-scrollbar-thumb {
    background-color: @gray-3;
    border-radius: 32px;
  }
  /*  */
  &::-webkit-scrollbar-track {
    background-color: #dbeffd;
    border-radius: 32px;
  }
  .top-bt {
    // padding: 0px @padding-20;
    display: flex;
    .revoke {
      color: @primary-color;
      cursor: pointer;
      padding-right: 30px;
      font-size: @font-size-16;
    }
    .report {
      font-size: @font-size-16;
      color: @warning;
      cursor: pointer;
    }
  }
  .noteCard {
    margin-top: 20px;
  }
  .form {
    .message {
      background: none;
      border: 1px solid rgba(148, 148, 148, 1);
      resize: none;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #949494;
      height: 56px;
      width: 282px;
      margin-top: 12px;
    }
    .bt {
      display: flex;
      margin-top: 3px;
      .name {
        width: 160px;
        height: 36px;

        border: 1px solid rgba(148, 148, 148, 1);

        background: none;
        line-height: 20px;
        margin-right: 30px;
      }
      .buttom {
        width: 80px;
        height: 36px;
        background: #202020;
        border-radius: 18px;
      }
    }
  }
  .title {
    margin-top: 25px;
    font-size: 14px;
    color: #202020;
    font-weight: 700;
  }
  .comments {
    margin-top: 10px;
    .comment-li {
      display: flex;
      margin-bottom: 30px;
      .user-head {
        width: 28px;
        height: 28px;
        border-radius: 50%;
        background-image: linear-gradient(
          180deg,
          rgba(29, 225, 104, 0.5) 0%,
          #1de168 100%
        );
      }
      .comment-right {
        width: 284px;
        margin-left: 8px;

        .momment-right-top {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .name {
            margin-right: 4px;
          }
          .time {
            font-size: 12px;
            color: #949494;
            font-weight: 400;
          }
          .delete {
            float: right;
            font-size: 12px;
            color: #949494;
            font-weight: 400;
          }
        }
        .comment-message {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #202020;
          font-weight: 400;
          word-wrap: break-word;
          width: 250px;
        }
      }
    }
  }
  .more {
    height: 20px;
    color: @gray-2;
    text-align: center;
    cursor: pointer;
    padding: 20px;
  }
}
</style>